    secodeOrder: [] as orderItem[],
    thirdOrder: [] as orderItem[],
    fourthOrder: [] as orderItem[],
    fifthOrder: [] as orderItem[],
    sixthOrder: [] as orderItem[]

});

const orderItemList = ref<orderItem[]>([]);

const userItem = ref<interfaceUser[]>([]);

//为tabs赋值
const tabs = ref<tabInterface[]>([{ laber: "今天", type: 0, active: true }, { laber: "昨天", type: 1, active: false }, { laber: "最近7天", type: 2, active: false }, { laber: "最近30天", type: 3, active: false }]);
//获取销量信息
const querySalesVolume = (item: tabInterface) => {
    //点击修改状态
    tabs.value.filter(item => item.active = false);
    item.active = true;
    status.value = item.type;
    salesVolumeOrder();
}

const findAll = async () => {
    const { data } = await findAllUser();
    userItem.value = data;
}


const orderclear = () => {
    order.firstOrder.length = 0;
    order.secodeOrder.length = 0;
    order.thirdOrder.length = 0;
    order.fourthOrder.length = 0;
    order.fifthOrder.length = 0;
    order.sixthOrder.length = 0;
}

const orderComponed = computed(() => {
    let firstMoney = 0;
    let secodeMoney = 0;
    let fifthMoney = 0;

    order.firstOrder.forEach(item => {
        firstMoney += item.actual_payment;
    })

    order.secodeOrder.forEach(item => {
        secodeMoney += item.actual_payment;
    })
    order.fifthOrder.forEach(item => {
        fifthMoney += item.actual_payment;
    })

    return { firstMoney, secodeMoney, fifthMoney }
})

const status = ref();

const salesVolumeOrder = async () => {

    debounce(async () => {
        loadingShow.value = true;
        orderclear();
        const { data } = await salesVolume(status.value);
        orderItemList.value = data;
        orderItemList.value.forEach(item => {
            if (item.status === 0) {
                order.firstOrder.push(item);
            } else if (item.status === 1) {
                order.secodeOrder.push(item);
            }
            else if (item.status === 2) {
                order.thirdOrder.push(item);
            }
            else if (item.status === 3) {
                order.fourthOrder.push(item);
            }
            else if (item.status === 4) {
                order.fifthOrder.push(item);
            } else if (item.status === 5) {
                order.sixthOrder.push(item);
            }
        })
        loadingShow.value = false;

    }, 500)

}
onMounted(() => {
    salesVolumeOrder();
    findAll();
    setTimeout(() => {
        salesVolumeOrder();
    }, 1000 * 30);

})

